.nav--container {
    text-align: left;
    padding: 15px 25px;
    background-color: #231c42;
    color: white;
    width: 12%;
    height: auto;
    position: static;
    margin: 0;
    border-bottom-right-radius: 0.75rem;
}

.content-wrapper {
    position: static;
    top: 20px;
}

.page-redirect {
    margin: 40px 0px;
    transition: opacity 0.2s;
}

.page-redirect.selected {
    color: rgb(157, 157, 199);
}

.page-redirect:hover {
    opacity: 60%;
    cursor: pointer;
}

.tab {
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: 0vh;
    padding: 20px;
    position: fixed;
}

.tabImage{
    margin-left: 12.5px;
    height: 3rem;
    width: 3rem;
}