.header-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
}
.portfolio-tile {
    padding: 5px;
    text-align: left;
    margin-bottom: 50px;
    border: 2px solid lightgray;
    border-radius: 1rem;
}

.imag {
    width: 100%;
    margin: 0;
}

.proj-short-sum {
    font-weight: 300;
    font-size: 16pt;
    padding: 10px 15px;
}

.github-logo {
    width: 3rem;
    height: 3rem;
    aspect-ratio: 1/1;
    margin: 0px 0px 0px 10px;
}