.text-container {
    text-align: left;
}

.messageForm {
    text-align: left;
}

.messageForm > p {
    font-size: 16px;
    margin: 0;
    font-weight: 600;
}
/* Style inputs */
input[type=text], select {
    padding: 8px 12px;
    margin-top: 8px;
    margin-bottom: 20px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
  
textarea {
    padding: 8px 12px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    width: 80%;
    margin-right: 50px;
    margin-bottom: 20px;
    margin-top: 8px;
    height: 10rem;
}


/* Style the submit button */
button  {
    width: 100%;
    font-size: 16px;
    background-color: #463b78;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
  
  /* Add a background color to the submit button on mouse-over */
  button:hover {
    background-color: #191047;
  }

  .err {
    opacity: 80%;
    padding: 5px;
    font-size: 14px;
    color: white;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 800;
    background-color: rgba(146, 0, 0, 0.8);
    border-radius: 0.5rem;
  }

  .success-mess {
    margin-top: 0;
    opacity: 75%;
    padding: 50px;
    font-size: 32px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  .success-message-readback {
    margin-top: 0;
    text-align: left;
    height: 35vh;
  }

  .subject {
    font-size: 14pt;
  }

  .message-container {
    font-size: 12pt;
    max-width: 75%;
    background-color: rgba(245, 245, 245, 0.673);
    padding: 10px;
    border: 1px solid rgba(169, 169, 169, 0.686);
    border-radius: 5px;
  }